<template>
  <div id="recover">
    <common-header :title="'回收礼物'"></common-header>
    <Gift />
    <Cell :title="'回收数量'" :type="'count'" :num="value" :max="5" @change="valueChange"/>

    <div class="recover__btn">
      <span class="btn__value">
        <span class="text">回收价：</span>
        <span class="num">￥170.0</span>
      </span>
      <span class="btn__submit">确认回收</span>
    </div>
  </div>
</template>

<script>
  import Gift from '@/components/Goods/Goods';
  import Cell from '@/components/Cell/Cell';
  export default {
    data() {
      return {
        value: 2
      }
    },
    methods: {
      valueChange(val) {
        this.value += val;
      }
    },
    components: {
      Gift,
      Cell
    }
  }
</script>

<style lang="scss">
  #recover {
    height: 100%;
    background: #f0f0f0;

    #giftDetail__good {
      margin-top: 0;
      border-bottom: 1px solid #eee;
    }

    .recover__btn {
      position: fixed;
      left: 0;
      bottom: 0;
      height: 1.307rem;
      line-height: 1.307rem;
      width: 100%;
      font-size: 0;
      text-align: right;
      background: #fff;
      border-top: 1px solid #eee;

      .btn__value {
        font-size: 0;

        .text {
          font-size: 0.435rem;
        }

        .num {
          padding-right: 0.4rem;
          font-size: 0.435rem;
          font-weight: 600;
          color: #fe4436;
        }
      }

      .btn__submit {
        float: right;
        display: inline-block;
        padding: 0 0.587rem;
        font-size: 0.435rem;
        color: #fff;
        background-image: linear-gradient(135deg, #fe9bba 0%, #de7fef 100%);
      }
    }
  }
</style>




